<template>
  <div>
    <div class="shopcart">
      <!--   -->
      <div class="content" @click="toggleList">
        <div class="content-left">
          <!--logo,-->
          <div class="logo-wrapper">  <!---->
            <div class="logo" :class="{'highlight':totalCount >0}">
              <i class="icon-shopping_cart" :class="{'highlight':totalCount > 0}"></i>
            </div>
            <!---->
            <div v-show="totalCount>0" class="num">{{totalCount}}</div>
          </div>
          <!---->
          <div class="price" :class="{'highlight':totalPrice > 0}">{{totalPrice}}</div>
          <div class="desc">{{deliveryPrice}}</div>
        </div>
        <div class="content-right" @click.stop.prevent="pay"> <!--  -->
          <div class="pay" :class="payClass"> <!---->
            <!--{{ minPrice }}-->
            {{payDesc}} <!----->
        </div>
        </div>
      </div>
      <!--   -->
      <div class="ball-container">
        <div v-for="(ball, index) in balls" :key="index">
          <transition name="drop" @before-enter="beforeDrop" @enter="dropping" @after-enter="afterDrop">
            <div class="ball" v-show="ball.show">
              <div class="inner inner-hook"></div>
            </div>
          </transition>
        </div>
      </div>

      <!--  -->
      <transition name="fold">
        <div class="shopcart-list" v-show="listShow">
          <div class="list-header">
            <h1 class="title"></h1>
            <span class="empty" @click="emptyShopCart"></span>
          </div>
          <div class="list-content" ref="listContent">
            <ul>
              <li class="food" v-for="(food, index) in selectFoods" :key="index">
                <span class="name">{{food.name}}</span>
                <div class="price">
                  <span>{{food.price*food.count}}</span>
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol @add="addFood" :food="food"></cartcontrol>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </transition>
    </div>
    <!--  -->
    <transition name="fade">
      <div class="list-mask" @click="hideList" v-show="listShow"></div>
    </transition>
  </div>
</template>

<script type="text/ecmascript-6">
  import BScroll from 'better-scroll'; // 
  import cartcontrol from '../cartcontrol/cartcontrol.vue'

  export default {
    props: { // goods
      selectFoods: { // 
        type: Array,
        default() { // vue ArrayObject
          return [
            {
              price: 0,
              count: 0
            }
          ];
        }
      },
      deliveryPrice: { // 
        type: Number,
        default: 0 // 
      },
      minPrice: { // 
        type: Number,
        default: 0
      }
    },
    data() {
      return {
        balls: [ // 
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          },
          {
            show: false
          }
        ],
        dropBalls: [], // 
        fold: true
      };
    },
    computed: {
      // 
      totalPrice() {
        let total = 0;
        this.selectFoods.forEach((food) => {
          total += food.price * food.count;
        });
        return total;
      },
      totalCount() { // 
        let count = 0;
        this.selectFoods.forEach((food) => {
          count += food.count;
        });
        return count;
      },
      payDesc() { // 
        if (this.totalPrice === 0) {
          // return ''+this.minPr';
          // ES6  `` ${}
          return `${this.minPrice}`;
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice;
          return `${diff}`;
        } else {
          return '';
        }
      },
      payClass() { // class
        if (this.ttalPrice < this.minPrice) {
          return 'no-enough';
       } else {
          return 'enough';
        }
      },
      listShow() { // 
        if (!this.totalCount) { // this.totalCount < 0
          this.fold = true;
          return false;
        }
        let show = !this.fold;
        if (show) {
          this.$nextTick(() => { //  BSCroll
            if (!this.scroll) {
              this.scroll = new BScroll(this.$refs.listContent, {
                click: true
              });
            } else {
              this.scroll.refresh(); // listShow Bscroll
            }
          });
        }
        return show;
      }
    },
    methods: {
      drop(el) {
        console.log(el);
        // true
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i];
          if (!ball.show) {
            ball.show = true;
            ball.el = el; // dom
            this.dropBalls.push(ball);
            return;
          }
        }
      },
      toggleList() { // 
        if (!this.totalCount) { // 
          return;
        }
        this.fold = !this.fold;
      },
      hideList() {
        this.fold = true;
      },
      emptyShopCart() {
        this.selectFoods.forEach((food) => {
          food.count = 0;
        });
      },
      pay() {
        if (this.totalPrice < this.minPrice) {
          return;
        }
        window.alert(`${this.totalPrice}`);
		this.emptyShopCart()
      },
      addFood(target) {
        this.drop(target);
      },
      beforeDrop(el) {
        let count = this.balls.length; // 
        while (count--) {
          let ball = this.balls[count];
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect(); // 
            let x = rect.left - 32; // 
            // 
            let y = -(window.innerHeight - rect.top - 22);
            el.style.display = ''; // 
            el.style.webkitTransform = `translate3d(0,${y}px,0)`;
            el.style.transform = `translate3d(0,${y}px,0)`;
            let inner = el.getElementsByClassName('inner-hook')[0];
            inner.style.webktiTransform = `transform3d(${x}px,0,0)`;
            inner.style.transform = `translate3d(${x}px,0,0)`;
          }
        }
      },
      dropping(el, done) { // enter leavedonevue
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight; // 
        this.$nextTick(() => {
          el.style.webkitTransform = 'translate3d(0,0,0)';
          el.style.transform = 'translate3d(0,0,0)';
          let inner = el.getElementsByClassName('inner-hook')[0];
          inner.style.webktiTransform = 'translate3d(0,0,0)';
          inner.style.transform = 'translate3d(0,0,0)';
          el.addEventListener('transitionend', done); // transitionend css3 transition
        });
      },
      afterDrop(el) {
        let ball = this.dropBalls.shift(); // 
        if (ball) {
          ball.show = false; // 
          el.style.display = 'none';
        }
      }
    },
    components: {
      cartcontrol
    }
  };
</script>

