import axios from 'axios';
import Vue from 'vue';
import router from './router.js'// {currentRoute,push,replace} -> currentRoute=={key:value,fullPath}

import store from '../plugins/vuex.js'

// import vm from '../main.js'
//
axios.interceptors.request.use(function(config) {
  // console.log('');
  //config   axios(config)
  
  //    token
  let token = store.state.user.user.token
  token = token ? token : ''
  config.headers = {
    token: token
  }
  
  //loading
  // vm.bLoading=true;
  store.commit('UPDATA_LOADING', true)
  return config;// 
}, function(error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function(response) {
  // console.log('',response.data.err);
  //response  ~~ axiosres
  
  //loading
  // vm.bLoading=false;
  store.commit('UPDATA_LOADING', false)  
  
  //    res.status   res.data.err == 2
  
  //tokenlogin,
  let currentRoute = router.currentRoute.fullPath;//string
  if(response.data.err===1 && !currentRoute.includes('/login') && !currentRoute.includes('/reg')){
    router.replace({
      path:'/login',
      query:{p:currentRoute}
    })
  }
  
  return response;//
}, function(error) {
  // 
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
window.axios=axios;
export default axios;